export default {
	data() {
		return {
			index: 0,
			array: ['1人', '2人', '3人', '4人', '5人', '6人', '7人', '8人', '9人', '10人', '10人以上'],
			date: new Date().toISOString().slice(0, 10),
			array1: ['食堂点餐', '沙发', '长桌', '双人桌'],
			i: 0,
			radio: '1',
			show: false,
			time: '12.00'
		}
	},
	onLoad(option) {
		console.log(option)
		this.time = option.time
	},
	methods: {
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value
		},
		clickNavi() {
			uni.navigateTo({
				url: '../index/time'
			})
		},
		clickPickerChange(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.i = e.target.value
		},
		onChange(event) {
			this.radio = event.detail
		},

		onClick(event) {
			console.log(event)
			const {
				name
			} = event.currentTarget.dataset;
			this.radio = name
			if (name == 2) {
				this.show = true
			} else {
				this.show = false
			}
		},
		clickNavi2() {
			uni.navigateTo({
				url: './take'
			})
		},
		submit() {
			uni.showToast({
				title: '预约成功',
				icon: 'success',
				duration: 2000
			})
		}
	}
}
